<template lang="pug">
.hello
  h1 {{ msg }}
  h3 
  h3 
  h3 
  h3 2
  h3 
    router-link.item(to="/foods" exact='') 
  //cards(:foods = "babyfoods")
</template>

<script>

import Cards from './Cards.vue';

export default {
  name: 'Baby',
  props: ['foods'],
  components: { Cards },
  data() {
    return {
      msg: 'DIY',
      mySearch: '',
      showPop: false,
      pop: '',
    };
  },
  computed: {
    babyfoods() {
      return this.foods.filter((f) => f.b);
    },
  },
  methods: {
    has(j, k) {
      if (!k) return true;
      return JSON.stringify(j).indexOf(k) > -1;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.animated .card {
  position: relative;
  top: 0;
}
.animated .card:hover {
  top: -2px;
  transition: all 0.3s ease;
}
</style>
